// Library Imports
import React, { Component } from "react";
import { connect } from "react-redux";

// Relative Imports
import { Content } from "../styles";
import Header from "../../UI/Header/Centered";
import Aux from "../../../Aux";

class SettingsDetail extends Component {
  render() {
    return (
      <Aux>
        <Header
          H1={"Currency Wallets"}
          P="Add/remove currency wallets"
          close={this.props.close}
          back={() => this.props.routing("Overview", "")}
        />
        <Content></Content>
      </Aux>
    );
  }
}

const mapStateToProps = state => ({
  nativeCurrency: state.NativeCurrency
});

export default connect(mapStateToProps, {})(SettingsDetail);
