// Library Imports
import React, { Component } from "react";

// Relative Imports
import { Container } from "./styles";

import NativeCurrency from "./NativeCurrency";
import CurrencyWallets from "./CurrencyWallets";
import Passwords from "./Passwords";

class SettingsDetail extends Component {
  Details = () => {
    switch (this.props.content) {
      case "Native Currency":
        return <NativeCurrency routing={this.props.routing} />;
      case "Currency Wallets":
        return <CurrencyWallets routing={this.props.routing} />;
      case "Passwords":
        return <Passwords routing={this.props.routing} />;
    }
  };

  render() {
    return <Container show={this.props.show}>{this.Details()}</Container>;
  }
}

export default SettingsDetail;
